<template>
  <div class="rights">
      <!-- 导航条 -->
    <el-row>
    <el-col :span="24">   
      <el-breadcrumb separator='/'>
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>权限列表</el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>
    </el-row>
    <!-- 权限列表 -->
    <el-table  
      v-loading="loading"
      :data = 'rightList'
      border
      style="width: 100%">
      <el-table-column
          type="index"
          width="50">
      </el-table-column>
      
      <el-table-column
          prop="authName"
          label="权限名称"
          width="100">
      </el-table-column>

      <el-table-column
          prop="path"
          label="路径"
          width="220">
      </el-table-column>

      <el-table-column
        prop="mobile"
        label="层级"
        width="220">
        <template slot-scope="scope">
          <span>{{scope.row.level | fmtLevel}}</span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import {getRightList} from "../../src/api";
export default {
  data(){
    return {
      rightList: [],
      loading: true
    }
  },
  filters: {
    // 过滤器,将数字转换为文字
    fmtLevel (level) {
      if (level === '0') {
        return '一级'
      }else if (level === '1') {
        return '二级'
      } else {
        return '三级'
      }
    }
  },
  created () {
    this.loading =true
    getRightList({type: "list"}).then(res => {
      // console.log(res)
      if (res.meta.status === 200) {
        this.rightList = res.data
        this.loading = false
      }
    })
  },
 
};
</script>
<style lang="scss" scoped>
</style>



